// CSS Reset start
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
  cursor: default;
}

h2, h4, h5 {
  line-height: 1em;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role='list'],
ol[role='list'] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
   scroll-behavior: auto;
  }
  
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

// CSS variables
:root {

    // background:
    --darker-blue-bg: #0F1D2A;
    --ligher-blue-bg: #1E3554;
    --darker-green-bg: #001510;
    --ligher-green-bg: #11283A;
    --light-accent-bg: #E9997F;

    // font color:
    --black: black;
    --white: white;
    --section-par-text-color: #97c5ff;    
    --section-title-text-shadow:  2px 2px 0px var(--ligher-blue-bg), -2px -2px 0px var(--ligher-blue-bg);
    --par-text-shadow: 2px 2px 3px var(--black);
    --hero-text-shadow: 0px 4px 12px black;

    // font size:
    --main-header-size: calc(12px + 4vmax);
    --main-header-size-mobile: calc(12px + 3vmax);
    --main-subheader-size: calc(12px + 2vmax);
    --main-subheader-handwriting-size: calc(12px + 3vmax);
    --paragraph-size: calc(12px + 1vmin);



    // font family:
    --font-main-heading: 'Rowdies', cursive;
    --font-sub-heading: 'Hammersmith One', sans-serif;
    --font-hand-writing: 'Dancing Script', cursive;
    --pragraph-font: 'Rubik', sans-serif;

    // transition durations:
    --default-transition-duration: 0.6s ease-out;
    --project-transition-duration: 1s ease-in-out;

}

///////////////////////////////////////
// SECTION 1 -- SECTION 1 -- SECTION 1 
///////////////////////////////////////

.sectionOne {
  user-select: none;
    width: 100%;
    height: 100%;
    display: flex;
    position: relative;
    .imageTextContainer {
        width: 100%;
        height: 100%;
        overflow: hidden;
        .heroText {
          position: absolute;
          overflow: hidden;
          z-index: 2;
          width: 100%;
          height: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          top: 15%;
          h1, h2 {
            transform: translateY(50px);
            transition: var(--default-transition-duration);
            text-align: center;
            padding: 2rem 2rem;
            text-shadow: var(--hero-text-shadow);
            white-space: nowrap;
            color: var(--black);
          }
          h1 {
            font-family: var(--font-main-heading);
            font-size: var(--main-header-size);
            margin-bottom: -2rem;
            clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0% 100%);
            transform: translateY(50px);
            @media screen and (max-width: 300px) {
              & {
                font-size: var(--main-header-size-mobile);
              }
            }
          }
          h2 {
            font-family: var(--font-sub-heading);
            font-size: var(--main-subheader-size);
            margin-top: -1rem;
            clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
            transform: translateY(-50px);
            line-height: normal;
            span {
              font-family: var(--font-hand-writing);
              font-size: var(--main-subheader-handwriting-size);
            }
          }
        }
        .heroImages {
            width: 100%;
            height: 100vh;
            display: flex;
            position: relative;
            overflow: hidden;
            .heroImg {
                position: absolute;
                object-fit: cover;
                height: 100%;
                width: 100%;
                bottom: 0;
                object-position: bottom;
            }
            .heroImg1 {
                transform: translateY(160px);
                z-index: 3;
                @media screen and (max-width: 1600px) {
                    & {
                        transform: translateY(70px);
                    }
                }
            }
            .heroImg2 {
                transform: translateY(50px);
                z-index: 2;
                @media screen and (max-width: 1600px) {
                    & {
                        transform: translateY(20px);
                    }
                }
            }
        }
    }
}

///////////////////////////////////////
// SECTION 2 -- SECTION 2 -- SECTION 2
///////////////////////////////////////

.sectionTwo {
  user-select: none;
  min-height: 100vh;
  width: 100%;
  background-color: var(--darker-blue-bg);
  z-index: 2;
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: row;
  padding-top: 5rem;
  padding-bottom: 5rem;
  @media screen and (max-width: 1000px) {
    & {
      flex-direction: column;
    }
  }
  .sectionTwoDiv {
    width: 100%;
  }
  .sectionTwoText {
    padding: 1em 0em 1em 1em;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 1100px;
    .sectionTwoTextElements {
      max-width: 70%;
      margin-top: -4rem;
      display: flex;
      flex-direction: column;
      gap: 1em;
      @media screen and (max-width: 1200px) {
        & {
          margin-top: -8rem;
        }
      }
      @media screen and (max-width: 1000px) {
        & {
          margin-top: -0rem;
        }
      }
      @media screen and (max-height: 62000px) {
        & {
          margin-top: 0rem;
        }
      }
      h2 {
        font-family: var(--font-sub-heading);
        font-size: var(--main-header-size);
        text-shadow: var(--darker-blue-bg);
        color: var(--darker-blue-bg);
        transform: translateY(50px);
        transition: var(--default-transition-duration);
        opacity: 0;
      }
      p {
        font-size: var(--paragraph-size);
        color: var(--section-par-text-color);
        transition: var(--default-transition-duration);
        opacity: 0;
        transform: translateY(50px);
        text-shadow: var(--par-text-shadow);
        font-family: var(--pragraph-font);
      }
    }
  }
  .sectionTwoImage {
    padding: 0em 2em 0em 0em;
    position: relative;
    width: 100%;
    min-height: 70vh;
    height: 100%;
    overflow: hidden;
    max-width: 1100px;
    @media screen and (max-width: 1000px) {
      & {
        padding: 2em;
        padding-top: 0em;
        display: flex;
        justify-content: center;
      }
    }
    div {
      width: fit-content;
      height: fit-content;
      overflow: hidden;
      margin: 0 auto;
      display: flex;
      justify-content: center;
      img  {
        height: 100%;
        width: 100%;
        max-width: 600px;
        object-fit: cover;
        margin: 0 auto;
        position: absolute;
        overflow: hidden;
        @media screen and (max-width: 1000px) {
          & {
            padding: 0em;
          }
        }
        @media screen and (min-width: 2000px) {
          & {
            min-width: 100%;
          }
        }
      }
      .headshotOutline {
        object-fit: fill;
        box-shadow: 
          24px 24px 0px 10px var(--darker-blue-bg),
          -24px -24px 0px 10px var(--darker-blue-bg),
          24px -24px 0px 10px var(--darker-blue-bg)
      }
    }
  }
  .mole {
    position: absolute;
    width: 25vw;
    left: 0;
    transform: translateY(-50px);
    @media screen and (max-width: 950px) {
      display: none;
    }
  }
  .bedrock {
    position: absolute;
    bottom: 0;
    z-index: -1;
    width: 70vw;
    transform: translateY(0rem);
    padding-right: 1rem;
    @media screen and (max-width: 1000px) {
      & {
        display: none;
      }
    }
  }
  .bedrockMobile {
    display: none;
    position: absolute;
    top: 15%;
    z-index: -1;
    width: 100vw;
    transform: translateY(0rem);
    @media screen and (max-width: 1000px) {
      & {
        display: block;
      }
    }
    @media screen and (max-width: 800px) {
      & {
        top: 23%;
      }
    }
    @media screen and (max-width: 600px) {
      & {
        top: 30%;
      }
    }
    @media screen and (max-width: 450px) {
      & {
        top: 37%;
      }
    }
    @media screen and (max-width: 350px) {
      & {
        top: 47%;
      }
    }
    @media screen and (max-width: 300px) {
      & {
        top: 51%;
      }
    }
  }
}

///////////////////////////////////////
// SECTION 3 -- SECTION 3 -- SECTION 3
///////////////////////////////////////

.sectionThree {
  user-select: none;
  content: '';
  padding-bottom: 8rem;
  width: 100%;
  background-color: var(--darker-blue-bg);
  overflow: hidden;
  @media screen and (max-width: 900px) {
    & {
      min-height: 40vh;
    }
  }
  .railroadContainer {
    width: 100%;
    overflow: hidden;
    padding: 2rem 0rem 0rem 0rem;
    transform: rotate(1.5deg) scale(1.01);
    position: relative;
    .railroadTrack {
      min-height: 200px;
      width: 100%;
      object-fit: cover;
    }
    .railroadTrackDesktop {
      display: block;
    }
    .railroadTrackMobile {
      display: none;
    }
    @media screen and (max-width: 700px) {
      .railroadTrackDesktop {
        display: none;
      }
      .railroadTrackMobile {
        display: block;
      }
    }
    .htmlCart {
      position: absolute;
      bottom: 0%;
      height: 19vmax;
      animation: moveCart 20s infinite linear;
    }
    .htmlCartWheel {
      animation: cartWheel 20s infinite linear;
    }
    .cssCart {
      position: absolute;
      bottom: 0%;
      left: -20%;
      height: 19vmax;
      animation: moveCart 20s infinite linear;
      animation-delay: 3s;
    }
    .cssCartWheel {
      animation: cartWheel 20s infinite linear;
      animation-delay: 3s;
      left: -20%;
    }
    .jsCart {
      position: absolute;
      bottom: 0%;
      left: -20%;
      height: 19vmax;
      animation: moveCart 20s infinite linear;
      animation-delay: 6s;
    }
    .jsCartWheel {
      animation: cartWheel 20s infinite linear;
      animation-delay: 6s;
      left: -20%;
    }
    .nodeCart {
      position: absolute;
      bottom: 0%;
      left: -20%;
      height: 19vmax;
      animation: moveCart 20s infinite linear;
      animation-delay: 9s;
    }
    .nodeCartWheel {
      animation: cartWheel 20s infinite linear;
      animation-delay: 9s;
      left: -20%;
    }
    .vueCart {
      position: absolute;
      bottom: 0%;
      left: -20%;
      height: 19vmax;
      animation: moveCart 20s infinite linear;
      animation-delay: 12s;
    }
    .vueCartWheel {
      animation: cartWheel 20s infinite linear;
      animation-delay: 12s;
      left: -20%;
    }
    .herokuCart {
      position: absolute;
      bottom: 0%;
      left: -20%;
      height: 19vmax;
      animation: moveCart 20s infinite linear;
      animation-delay: 15s;
    }
    .herokuCartWheel {
      animation: cartWheel 20s infinite linear;
      animation-delay: 15s;
      left: -20%;
    }
    @keyframes moveCart {
      0% {
        left: -20%;
      }
      100% {
        left: 120%;
      }
    }
    .leftWheel {
      transform-origin: 32% 85%;
    }
    .rightWheel {
      transform-origin: 67% 85%;
    }
    @keyframes cartWheel {
      0% {
        transform: rotate(0deg);
        left: -20%;
      } 
      100% {
        transform: rotate(5000deg);
        left: 120%;
      }
    }
  }
}

///////////////////////////////////////
// SECTION 4 -- SECTION 4 -- SECTION 4
///////////////////////////////////////

.sectionFour {
  width: 100%;
  background-color: var(--darker-blue-bg);
  .project {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    &:nth-child(even) {
      flex-direction: row-reverse;
    }
    @media screen and (max-width: 1000px) {
      flex-direction: column;
      &:nth-child(even) {
        flex-direction: column;
      }
    }
    .sectionFourImg {
      width: 100%;
      padding: 1rem;
      max-width: 900px;
      display: flex;
      align-self: center;
      div {
        width: 100%;
        height: 100%;
        position: relative;
        overflow: hidden;
        .projectCover {
          width: 100%;
        }
        .overlay {
          position: absolute;
          top: 0;
          transition: 0.5s ease-in;
          transform-style: preserve-3d;
        }
        .bones {
          transform: perspective(500px) translateZ(20px) rotateY(0deg);
        }
        .projectImgOne {
          transform: perspective(500px) translateZ(20px) rotateY(-90deg);
        }
        .projectImgTwo {
          transform: perspective(1000px) translateZ(20px) rotateY(-90deg);
        }
        .projectVideo {
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          transition: 0.7s ease-in;
          opacity: 0;
          .videoElement {
            transition: 1s ease-out;
            border-radius: 150px;
            width: 100%;
            height: 100%;
            object-fit: contain;
            transform: scale(0.75);
          }
        }
      }

      
      // Project 1
      .viewPerformante:hover .projectImg {
        opacity: 0;
      }
      .viewPerformante:hover .projectOneVideoEl {
        opacity: 1;
      }

      // Project 2
      .viewTwitter:hover .projectImg {
        opacity: 0;
      }
      .viewTwitter:hover .projectTwoVideoEl {
        opacity: 1;
      }

      // Project 3
      .viewVmm:hover .projectImg {
        opacity: 0;
      }
      .viewVmm:hover .projectThreeVideoEl {
        opacity: 1;
      }

      // Project 4
      .viewEmpire:hover .projectImg {
        opacity: 0;
      }
      .viewEmpire:hover .projectFourVideoEl {
        opacity: 1;
      }

      
    }
    .sectionFourText {
      width: 100%;
      padding: 1rem;
      display: flex;
      flex-direction: column;
      align-self: center;
      max-width: 900px;
      h4 {
        padding: 1rem 2rem 1rem 2rem;
        font-family: var(--font-sub-heading);
        font-size: var(--main-header-size);
        transition: var(--default-transition-duration);
        opacity: 0;
        text-shadow: var(--darker-blue-bg);
        transform: translateY(50px);
        color: var(--darker-blue-bg);
      }
      p {
        font-family: var(--pragraph-font);
        font-size: var(--paragraph-size);
        color: var(--darker-blue-bg);
        transition: var(--default-transition-duration);
        opacity: 0;
        transform: translateY(50px);
        text-shadow: var(--darker-blue-bg);
        padding: 0rem 2rem 0rem 2rem;
      }
      a {
        margin: 2rem 2rem 2rem 2rem;
        max-width: 170px;
        cursor: pointer;
        button {
          width: 100%;
          padding: 3px 0px;
          height: 100%;
          cursor: pointer;
          font-family: var(--font-sub-heading);
          border: solid 2px var(--section-par-text-color);
          color: var(--section-par-text-color);
          background-color: var(--black);
          border-radius: 5px;
          transition: var(--default-transition-duration);
          opacity: 0;
          transform: translateY(50px);
          &:hover {
            border: solid 2px var(--section-par-text-color);
            color: var(--black);
            background-color: var(--section-par-text-color);
          }
        }
      }
      @media screen and (max-width: 400px) {
        h4 {
          padding: 1rem;
        }
        p {
          padding: 0rem 1rem 0rem 1rem;
        }
      }
    }
  }
}

///////////////////////////////////////
// SECTION 5 -- SECTION 5 -- SECTION 5
///////////////////////////////////////

.sectionFive {
  width: 100%;
  background-color: var(--black);
  display: flex;
  flex-direction: column;
  align-items: center;
  .oilPatch {
    width: 100%;
  }
  .serviceContainer {
    background-color: var(--black);
    display: flex;
    flex-direction: row;
    width: 100%;
    padding: 0rem 2rem 2rem 2rem;
    gap: 1rem;
    min-height: 80vh;
    max-width: 1900px;
    .step {
      min-width: 120px;
      border-radius: 15px;
      transition: 2s;
      overflow: hidden;
      width: 0%;
      position: relative;
      transition: var(--default-transition-duration);
      img {
        position: absolute;
        min-height: 100%;
        min-width: 100%;
        object-fit: cover;
        filter: brightness(1);
        transition: var(--default-transition-duration);
      }
      h4, h5, p {
        color: var(--black);
        margin: 1rem;
        position: relative;
        z-index: 2;
        transition: var(--default-transition-duration);
        text-shadow: 
          2px 2px 1px var(--section-par-text-color),
          -2px -2px 1px var(--section-par-text-color);
      }
      h4 {
        font-family: var(--font-main-heading);
        width: fit-content;
        margin: 20px;
      }
      h5 {
        font-family: var(--font-sub-heading);
        font-size: var(--main-subheader-size);
        opacity: 0;
        padding-left: 15px;
        font-size: 0px;
      }
      p {
        color: var(--white);
        font-weight: bolder;
        font-family: var(--pragraph-font);
        text-shadow: 
          3px 3px 6px var(--darker-blue-bg),
          -3px -3px 6px var(--darker-blue-bg);
        padding: 1rem;
        font-size: 0px;
        opacity: 0;
      }
    }
    .active {
      width: 100%;
      max-height: none;
      img  {
        filter: brightness(0.5);
      }
      h4 {
        text-shadow: 2px 2px 1px var(--white),
        -2px -2px 1px var(--white);
      }
      h5 {
        opacity: 1;
        font-size: var(--main-subheader-size);
      }
      p {
        font-size: var(--paragraph-size);
        opacity: 1;
        margin: 1rem 3em 0rem 3em;
        max-width: 400px;
      }
    }
    @media screen and (max-width: 1000px) {
      & {
        flex-direction: column;
        .step {
          width: 100%;
          max-height: 75px;
          transition: var(--default-transition-duration);
          h4, h5, p {
            padding: 0px 5px 0px 8px;
          }
          h4 {
            margin: 20px;
          }
          h5 {
            opacity: 0;
            font-size: 0px;
            margin: 0.3rem;
          }
          p {
            opacity: 0;
            font-size: var(--paragraph-size);
            margin: 0;
            max-width: none;
          }
        }
        .active {
          min-height: 60vh;
          max-height: none;
          h4 {
            text-shadow: 2px 2px 1px var(--white),
            -2px -2px 1px var(--white);
          }
          h5 {
            opacity: 1;
            font-size: var(--main-subheader-size);
          }
          p {
            opacity: 1;
            padding: 1rem;
            font-size: var(--paragraph-size);
          }
        }
      }
    }
  }
}

///////////////////////////////////////
// SECTION 6 -- SECTION 6 -- SECTION 6
///////////////////////////////////////

.sectionSix {
  width: 100%;
  min-height: 100vh;
  background-color: var(--darker-blue-bg);
  display: flex;
  justify-content: center;
  flex-direction: row-reverse;
  align-items: center;
  @media screen and (max-width: 1000px) {
    & {
      flex-direction: column-reverse;
    }
  }
  .contactUs {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
    max-width: 800px;
    .formContainer {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      @media screen and (max-width: 1000px) {
        align-items: center;
      }
      form {
        display: flex;
        flex-direction: column;
        gap: 1.2rem;
        padding: 2rem;
        max-width: 550px;
        margin: 1rem;
        border-radius: 10px;
        box-shadow:  10px 10px 20px #091118;
        border: 2px solid var(--section-par-text-color);
        @media screen and (max-width: 400px) {
          & {
            padding: 1rem;
          }
        }
        label {
          color: var(--white);
          text-shadow: var(--par-text-shadow);
          font-family: var(--pragraph-font);
          font-size: var(--paragraph-size);
        }
        input {
          width: 100%;
          background-color: var(--ligher-blue-bg);
          border: none;
          padding: 7px 10px;
          border-radius: 5px;
          color: var(--white);
          box-shadow: inset 1px 1px 8px 0px var(--black);
          &:placeholder-shown {
            font-family: var(--pragraph-font);
          }
        }
        textarea {
          box-shadow: inset 1px 1px 8px 0px var(--black);
          width: 100% !important;
          max-width: 100%;
          resize: vertical;
          min-height: 200px;
          max-height: 700px;
          background-color: var(--ligher-blue-bg);
          border: none;
          padding: 7px 10px;
          border-radius: 5px;
          color: var(--white);
          &:placeholder-shown {
            font-family: var(--pragraph-font);
          }
        }
        button {
          cursor: pointer;
          font-family: var(--font-sub-heading);
          border: solid 2px var(--section-par-text-color);
          color: var(--section-par-text-color);
          background-color: var(--black);
          border-radius: 5px;
          padding: 3px 0px;
          transition: var(--default-transition-duration);
          &:hover {
            border: solid 2px var(--section-par-text-color);
            color: var(--black);
            background-color: var(--section-par-text-color);
          }
        }
      } 
    }
    .conactUsText {
      h4, p {
        transition: var(--default-transition-duration);
        transform: translateY(50px);
        color: var(--darker-blue-bg);
        opacity: 0;
      }
      h4 {
        text-shadow: var(--section-title-text-shadow);
        font-family: var(--font-sub-heading);
        font-size: var(--main-header-size);
        padding: 0rem 2rem 1rem 2rem;
      }
      p {
        text-shadow: var(--par-text-shadow);
        font-family: var(--pragraph-font);
        font-size: var(--paragraph-size);
        padding: 0rem 2rem 2rem 2rem;
      }
    }
    .contactUsImgContainer {
      position: relative;
      overflow: hidden;
